
import * as React from 'react';
import { RenderNodeProps } from '@ali/4ever-cangjie';
import { PlaceholderWrapper } from '@ali/4ever-component';

interface ForbiddenProps {
  attributes: RenderNodeProps['attributes'];
}

const Forbidden: React.FC<ForbiddenProps> = (
  props,
) => {
  const { attributes, children } = props;
  return (
    <PlaceholderWrapper data-testid="refblock-forbidden" {...attributes}>
      {children}
    </PlaceholderWrapper>
  );
};

export default Forbidden;
